import React from 'react';
import { connect } from '../store/Connect';

const CurrentSpot = ({
    left,
    top,
    hide,
}) => (hide === false &&
    <div>
        <span
            className={`cq-spot ${hide ? 'hide' : ''}`}
            style={{ top, left }}
        />
    </div>
);

export default connect(({ currentSpot: cs }) => ({
    left: cs.left,
    top: cs.top,
    hide: cs.hide,
}))(CurrentSpot);
